// @flow
import React from 'react';
import Cycle from './lib/Cycle.react';
import PegSprite from './PegSprite.react';

import type {PegType, PegFrame, PegEmote} from '../types';

type AnimationConfig = {
  frames: Array<PegFrame>,
  duration?: number,
  repeat?: number,
};

type Key = 'entry' | 'shake' | 'dance' | 'jump' | 'duck';

const ANIMATIONS: {[key: Key]: AnimationConfig} = {
  entry: {
    frames: ['front'],
  },
  shake: {
    frames: ['lean', 'front'],
    duration: 0.10,
    repeat: 2,
  },
  dance: {
    frames: ['jump', 'front'],
    duration: 0.7,
    repeat: -1,
  },
};

type State = {
  key: Key,
};

type Props = {
  onCycle: Function,
  isExcited?: boolean,
  isBuzzed?: boolean,
  emote?: PegEmote,
  alive: number,
  id: string,
  type: PegType,
  z: number,
};

function getKey(props: Props): Key {
  if (props.isExcited) return 'dance';
  if (props.isBuzzed) return 'shake';
  return 'entry';
}

class PegAnimator extends React.PureComponent {
  props: Props;
  state: State = {key: 'entry'};

  componentWillReceiveProps(props: Props) {
    const key = getKey(props);
    if (key !== this.state.key) {
      this.setState({key});
    }
  }

  _renderAnimationFrame = (i: number): React$Element<any> => {
    const {key} = this.state;
    const {frames} = ANIMATIONS[key];
    return (
      <PegSprite
        id={this.props.id}
        frame={frames[Math.min(frames.length - 1, i)]}
        alive={this.props.alive}
        type={this.props.type}
        z={this.props.z}
      />
    );
  };

  render() {
    const {emote, onCycle} = this.props;
    const key = getKey(this.props);
    const {frames, duration, repeat} = ANIMATIONS[key];
    return (
      <Cycle
        id={emote ? emote.at : key} // emotes are restartable
        length={frames.length}
        duration={duration}
        repeat={repeat}
        onRepeat={onCycle}
        render={this._renderAnimationFrame}
      />
    );
  }
}

export default PegAnimator;



