// @flow
import React from 'react';
import PegMotion from './PegMotion.react';
import {onTouchPeg} from '../interactions';

import type {Coords, PegType} from '../types';

type Props = {
  type: PegType,
  pos: Coords,
  isExcited?: boolean,
  isBuzzed?: boolean,
  alive: number,
  id: string,
  pos: Coords,
};

const Peg = (props: Props) => (
  <div onClick={() => onTouchPeg(props.id)}>
    <PegMotion {...props} />
  </div>
);

export default Peg;



