// @flow
import React from 'react';
import FlipX from './FlipX.react';
import BoardPosition from './BoardPosition.react';
import PegAnimator from './PegAnimator.react';
import {areEqual} from '../utils';
import {jump} from '../redux/ActionCreators';
import Store from '../redux/Store';

import {TweenMax, TimelineMax, Bounce, Power0, Sine} from 'gsap';
import 'gsap-react-plugin';

import type {Peg as PegProps, Coords, PegEmote} from '../types';

type Props = PegProps & {
  isExcited?: boolean,
  isBuzzed?: boolean,
  alive: number,
  id: string,
  pos: Coords,
  emote?: PegEmote,
};

type State = {
  direction: -1 | 1,
  x: number,
  y: number,
  z: number,
};

const MOVE_DURATION = 0.25;
const MOVE_CROUCH_DURATION = 0.08;
const HOP_HEIGHT = 100;
const FALL_IN_DURATION = 1;

const CROUCH_Z = -50;

const onJumpStart = () => Store.dispatch(jump());

class PegMotion extends React.Component {
  props: Props;
  state: State;

  _tween: Object;

  constructor(props: Props) {
    super(props);
    const {x, y} = props.pos;
    this.state = {
      direction: -1,
      x,
      y,
      z: 600,
    };
    this._tween = TweenMax.to(this, FALL_IN_DURATION, {
      state: {z: CROUCH_Z},
      ease: Bounce.easeOut,
      onComplete: () => this.setState({z: 0}),
    });
  }

  componentWillReceiveProps(props: Props) {
    if (!areEqual(props.pos, this.props.pos)) {
      // tween into position
      this._move(props.pos.x, props.pos.y);
      // TweenMax.to(this, MOVE_DURATION, {state: {...props.pos}});
    }
  }

  _move(x: number, y: number) {
    console.log('move', x, y);
    const direction = x > this.state.x ? 1 : -1;
    this.setState({
      direction,
      z: CROUCH_Z,
    });
    const xyTween = TweenMax.to(this, MOVE_DURATION, {
      state: {x, y},
      ease: Power0.easeNone,
    });
    const zTween = new TimelineMax()
      .to(this, MOVE_DURATION / 2, {state: {z: HOP_HEIGHT}, ease: Sine.easeOut})
      .to(this, MOVE_DURATION / 2, {state: {z: CROUCH_Z}, ease: Sine.easeIn})
      .to(this, MOVE_CROUCH_DURATION, {state: {z: 0}});
    new TimelineMax({
      delay: MOVE_CROUCH_DURATION,
      tweens: [zTween, xyTween],
      onStart: onJumpStart,
    });
  }

  _onCycle = () => {
    this.setState({direction: -this.state.direction});
  };

  render() {
    const {id, alive, type, isExcited, isBuzzed, emote} = this.props;
    const {direction, z} = this.state;
    return (
      <BoardPosition x={this.state.x} y={this.state.y}>
        <FlipX direction={direction}>
          <PegAnimator
            x={0}
            y={0}
            z={z}
            id={id}
            alive={alive}
            type={type}
            isExcited={isExcited}
            isBuzzed={isBuzzed}
            emote={emote}
            onCycle={this._onCycle}
          />
        </FlipX>
      </BoardPosition>
    );
  }
}

export default PegMotion;



