// @flow
import React from 'react';
import Group from './lib/Group.react';
import {BOARD_SIZE, BOARD_GRID_SIZE, BOARD_ROW_HEIGHT} from '../constants';

type Vec2 = {x: number, y: number};

function getXOffset(y) {
  return BOARD_SIZE - y * BOARD_GRID_SIZE / 2;
}

function boardToScreenPosition(pos: Vec2): Vec2 {
  return {
    x: pos.x * BOARD_GRID_SIZE + getXOffset(pos.y),
    y: pos.y * BOARD_ROW_HEIGHT,
  };
}

type Props = {
  x: number,
  y: number,
  children?: React.Element<any>,
};

const BoardPosition = (props: Props) => (
  <Group {...boardToScreenPosition(props)}>
    {props.children}
  </Group>
);

export default BoardPosition;



