// @flow
import React from 'react';
import Image from './lib/Image.react';
import sprites from '../sprites/peg/peg';
import shadowSVG from './images/shadow.svg';
import lerp from 'lerp';

import type {PegType, PegFrame} from '../types';

export type Props = {
  frame: PegFrame,
  alive: number,
  z: number, // added to y to simulate falling in
  type: PegType,
};

const getFrame = (props: Props): PegFrame => {
  if (props.alive < 1) return 'hurt';
  if (props.z > 0) return 'jump';
  if (props.z < 0) return 'duck';
  return props.frame;
};

const PegSprite = (props: Props) => {
  const {alive, type, z} = props;
  // provide a buffer during the fall to pause in the duck animation
  const flooredZ = Math.max(z, 0);
  const groundNearnessFactor = lerp(1, 0, flooredZ / 600);
  const {src, pivot, size} = sprites[type][getFrame(props)];
  return (
    <div>
      <Image
        src={shadowSVG}
        alpha={alive * groundNearnessFactor}
        scale={groundNearnessFactor}
        width={55}
        height={34}
      />
      <Image
        alpha={alive}
        src={src}
        pivot={pivot}
        width={size.w}
        height={size.h}
        y={-flooredZ}
      />
    </div>
  );
};

export default PegSprite;



