// @flow
import React from 'react';

type Props = {
  direction: 1 | -1,
  children?: React$Element<any>,
};

const FlipX = (props: Props) => (
  <div style={{transform: `scaleX(${props.direction})`}}>
    {props.children}
  </div>
);

export default FlipX;



