// @flow
import type {Action} from '../ActionCreators';
import type {PegEmote} from '../../types';

export type EmotesState = {
  [key: string]: PegEmote,
};

export default function reducer(
  state: EmotesState = {},
  action: Action,
): EmotesState {
  switch (action.type) {
    case 'BUZZ':
      return {
        ...state,
        [action.id]: {type: 'shake', at: Date.now()},
      };
    case 'EXCITED':
      return {
        ...state,
        [action.id]: {type: 'dance', at: Date.now()},
      };
    default:
      return state;
  }
}



