// @flow
import React from 'react';
import {TweenMax, SteppedEase} from 'gsap';
import 'gsap-react-plugin';

type Props = {
  id: any, // controls retriggering the same cycle
  length: number,
  duration?: number,
  repeat?: number,
  render: (i: number) => React.Element<any>,
  onRepeat?: Function,
};

type State = {
  i: number,
};

class Cycle extends React.Component {
  props: Props;
  state: State = {i: 0};

  _tween: ?Object;
  _i: number = 0; // migrated to state by tween

  componentWillUpdate(nextProps: Props) {
    if (nextProps.id !== this.props.id) {
      this.setState({i: 0});
      this._i = 0;
      TweenMax.killTweensOf(this);
      const {length, duration, repeat} = nextProps;
      this._tween = TweenMax.to(this, duration, {
        _i: length - 1,
        repeat,
        ease: new SteppedEase(1),
        onUpdate: this._onUpdate,
        onRepeat: this.props.onRepeat,
      });
    }
  }

  _onUpdate = () => {
    if (this.state.i !== this._i) {
      this.setState({i: this._i});
    }
  };

  componentWillUnmount() {
    TweenMax.killTweensOf(this);
  }

  render() {
    const {i} = this.state;
    return this.props.render(i);
  }
}

export default Cycle;



